<template>
  <div id="album">
    <classify v-bind:classify="classify.type1" v-bind:current="current[0]"></classify>
    <classify v-bind:classify="classify.type2" v-bind:current="current[1]"></classify>
    <h2></h2>
    <div class="albums">
      <album v-bind:album="album[0]"></album>
      <album v-bind:album="album[0]"></album>
      <album v-bind:album="album[0]"></album>
      <album v-bind:album="album[0]"></album>
      <album v-bind:album="album[0]"></album>

      <album v-bind:album="album[0]"></album>
      <album v-bind:album="album[0]"></album>
      <album v-bind:album="album[0]"></album>
      <album v-bind:album="album[0]"></album>
      <album v-bind:album="album[0]"></album>
    </div>
    <page v-bind:page="page"></page>
    <footer></footer>
  </div>
</template>
<script>
import classify from './singer/classify.vue'
import album from './singer/album.vue'
import page from './singer/page.vue'
export default{
  data () {
    return {
      classify: {
        type1: {
          title: '',
          content: ['', '', '', '', '', '', '']},
        type2: {
          title: '',
          content: ['', '', '', '', '', '', '', '', '', '']}
      },
      current: [{cur: ''}, {cur: ''}],
      page: {total: 50, current: 1},
      album: [{name: '', author: '', time: '2017-11-23', src: '/static/image/album1.jpg'}]
    }
  },
  components: {
    classify,
    album,
    page
  }
}
</script>
<style scoped>
  #album{
    margin: 0 100px;
  }
  #album> .albums{
    display: flex;
    flex-wrap: wrap;
  }
  footer{
    height: 100px;
    margin: 0 -100px;
    background-color: rgb(51,51,51);
  }
</style>



