<template>
  <div  id="homepage">
    <div id="recommend">
      <mytitle title=""></mytitle>
      <nav3 v-bind:title="navTitle.recommend" v-bind:current="current"></nav3>
      <gedan v-bind:current="current" v-bind:totalGedan="totalGedan"></gedan>
    </div>
    <div id="newSong">
      <mytitle title=""></mytitle>
      <nav3 v-bind:title="navTitle.newSong" v-bind:current="current"></nav3>
      <songs></songs>
    </div>
    <div id="newAlbum">
      <mytitle title=""></mytitle>
      <nav3 v-bind:title="navTitle.newAlbum" v-bind:current="current"></nav3>
      <albums></albums>
    </div>
    <div id="rank">
      <mytitle title=""></mytitle>
      <ranks></ranks>
    </div>
    <div id="mvs">
      <mytitle title="MV"></mytitle>
      <nav3 v-bind:title="navTitle.newSong" v-bind:current="current"></nav3>
      <mvs></mvs>
    </div>
    <footer></footer>
  </div>
</template>
<script>
import mytitle from './homePage/title.vue'
import nav3 from './homePage/nav3.vue'
import gedan from './homePage/gedan.vue'
import songs from './homePage/songs.vue'
import albums from './homePage/albums.vue'
import ranks from './homePage/ranks.vue'
import mvs from './homePage/mvs.vue'
export default{
  data () {
    return {
      navTitle: {
        recommend: ['', 'DJ', '', '90', '', ''],
        newSong: ['', '', '', '', ''],
        newAlbum: ['', '', '', '', ''],
        mv: ['', '', '', '', '', '']
      },
      current: {
        num: 0
      },
      totalGedan: [
        {src: '/static/image/gedan1.jpg', description: '1', popularity: ''},
        {src: '/static/image/gedan2.jpg', description: '2', popularity: ''},
        {src: '/static/image/gedan3.jpg', description: '3', popularity: ''},
        {src: '/static/image/gedan4.jpg', description: '4', popularity: ''},
        {src: '/static/image/gedan5.jpg', description: '5', popularity: ''},
        {src: '/static/image/gedan6.jpg', description: '6', popularity: ''},
        {src: '/static/image/gedan7.jpg', description: '7', popularity: ''},
        {src: '/static/image/gedan8.jpg', description: '8', popularity: ''},
        {src: '/static/image/gedan9.jpg', description: '9', popularity: ''},
        {src: '/static/image/gedan10.jpg', description: '0', popularity: ''}
      ],
      arrow: false
    }
  },
  components: {
    mytitle,
    nav3,
    gedan,
    songs,
    albums,
    ranks,
    mvs
  },
  methods: {
  }
}
</script>
<style scoped>
  #recommend,#newSong,#newAlbum,#mvs{
    position: relative;
    height: 550px;
    background: linear-gradient(rgb(242,242,242), white);
    overflow: hidden;
    margin-top: 0;
  }
  #newAlbum{
    height: 800px;
  }
  #rank{
    background: linear-gradient(rgb(242,242,242), white);
  }
  #mvs{
    height: 600px;
  }
  footer{
    height: 100px;
    background-color: rgb(51,51,51);
  }
</style>



