<script>
import classify from './singer/classify.vue'
import page from './singer/page.vue'
import songSheet from './homePage/songSheet.vue'
export default{
  data () {
    return {
      classify: {
        type1: {
          title: '',
          content: ['', '', '', 'KTV', '', '', '', '', '',
            '', '', '', '', '', '', '', '', '']}
      },
      current: [{cur: ''}, {cur: ''}, {cur: ''}],
      page: {total: 50, current: 1},
      gedan: [
        {src: '/static/image/gedan1.jpg', description: '1', popularity: ''},
        {src: '/static/image/gedan2.jpg', description: '2', popularity: ''},
        {src: '/static/image/gedan3.jpg', description: '3', popularity: ''},
        {src: '/static/image/gedan4.jpg', description: '4', popularity: ''}
      ]
    }
  },
  components: {
    classify,
    page,
    songSheet
  }
}
</script>
<style scoped>
  #classify{
    margin: 0 100px;
  }
  #classify> .gedans{
    display: flex;
    flex-wrap: wrap;
  }
  footer{
    height: 100px;
    margin: 0 -100px;
    background-color: rgb(51,51,51);
  }
</style>



