<template>
  <div id="MV">
    <classify v-bind:classify="classify.type1" v-bind:current="current[0]"></classify>
    <classify v-bind:classify="classify.type2" v-bind:current="current[1]"></classify>
    <classify v-bind:classify="classify.type3" v-bind:current="current[2]"></classify>
    <h2>MV</h2>
    <div class="mvs">
      <mv v-bind:mv="MVs[0]"></mv>
      <mv v-bind:mv="MVs[0]"></mv>
      <mv v-bind:mv="MVs[0]"></mv>
      <mv v-bind:mv="MVs[0]"></mv>
      <mv v-bind:mv="MVs[0]"></mv>
      <mv v-bind:mv="MVs[0]"></mv>
      <mv v-bind:mv="MVs[0]"></mv>
      <mv v-bind:mv="MVs[0]"></mv>
    </div>
    <page v-bind:page="page"></page>
    <footer></footer>
  </div>

</template>
<script>
  import classify from './singer/classify.vue'
  import page from './singer/page.vue'
  import mv from './singer/mv.vue'
  export default{
    data () {
      return {
        classify: {
          type1: {
            title: '',
            content: ['', '', '', '', '', '', '']},
          type2: {
            title: '',
            content: ['', '', '', '', '', '', '', '', '',
              '', '', '', '', '', 'KTV', '', '', '']},
          type3: {
            title: '',
            content: ['', '2017', '2016', '2015', '2014', '2013', '2012', '2011', '00', '']}
        },
        current: [{cur: ''}, {cur: ''}, {cur: ''}],
        page: {total: 50, current: 1},
        MVs: [{name: '', author: '', popular: '28w', time: '2017-11-20', src: './static/image/mv1.jpg'}]
      }
    },
    components: {
      classify,
      page,
      mv
    }
  }
</script>
<style scoped>
  #MV{
    margin: 0 100px;
  }
  #MV > .mvs{
    display: flex;
    flex-wrap: wrap;
  }
  footer{
    height: 100px;
    margin: 0 -100px;
    background-color: rgb(51,51,51);
  }

</style>



