// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import vuex from 'vuex'

Vue.config.productionTip = false
Vue.use(vuex)
var store = new vuex.Store({
  state: {
    songList: [{
      name: '',
      author: '',
      time: '04:25',
      src: '/static/music/ - .mp3'
    }, {name: 'faded',
      author: 'Alan Walker',
      time: '03:32',
      src: '/static/music/Alan Walker - Faded.mp3'
    }, {name: '',
      author: ' ',
      time: '05:33',
      src: '/static/music/  - .mp3'
    }, {name: '',
      author: '',
      time: '03:35',
      src: '/static/music/ - .mp3'
    }, {name: '',
      author: '',
      time: '05:07',
      src: '/static/music/-.mp3'}],
    currentIndex: 0
  }
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



